*{
  font-family: "Recursive", sans-serif;
}
.registration-form {
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: 0 auto;
    padding: 10% 0;
    padding-bottom: 0;
  }
  .registration-form input {
    box-sizing: border-box;
    font-weight: 500;
    font-size: 14px;
    border-radius: 25px;
    border: 1px solid rgb(175, 175, 175);
    height: 50px;
    margin-bottom: 15px;
    padding: 0px 24px;
  }
  .registration-form input:focus {
    outline: none;
    border: 2px solid rgb(104, 103, 103);
  }
  .registration-form button {
    box-sizing: border-box;
    font-weight: 500;
    font-size: 14px;
    border-radius: 25px;
    height: 50px;
    margin: 15px 0px;
    border: none;
    color: white;
    text-transform: uppercase;
    background-color: #50802C;
    cursor: pointer;
  }
  .registration-form button:hover,
  .registration-form button:active{
    background-color: #4c7929;
  }
  .registration-form .login-message {
    color: lightgray;
    text-align: center;
    margin: 0;
    font-size: 14px;
  }
  .registration-form .flex {
    display: flex;
    justify-content: space-between;
    width: 70%;
    margin: 15px auto;
  }
  .flex{
      display: flex;
  }
  .privacy-terms {
    font-weight: 500;
    font-size: 12px;
    text-align: center;
    padding: 15px 0px;
  }
  .animate__animated {
    --animate-duration: 0.5s;
  }
  .social a {
    text-align: center;
    width: 40px;
    height: 40px;
    float: left;
    background: #fff;
    margin: 0 10px 10px 0;
  }
  .social a img{
    width: 100%;
    height: 100%;
  }
  h2{
    display: none;
    color: #FB8F07;
    font-size: 20px;
    text-align: center;
    padding: 30px 0px;
  }
  section{
    display: flex;
  }
  .registration-aside{
    display: none;
    flex-basis: 50%;
    background-color: #4F802C;  
    flex-direction: column;
    justify-content: center;
    align-items: center;
    clip-path: polygon(10% 0, 100% 0, 100% 100%, 0% 100%);
  }
  .registration-aside h2{
    color: white;
    font-size: 25px;
   }
   .registration-aside p{
     padding: 10px 0px;
   }
   .registration-aside .link-login{
    width: 200px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    text-transform: uppercase;
    color: white;
    font-size: 12px;
    background-color: inherit;
    border: 1px solid white;
    border-radius: 5px;
    margin: 10px 0px;
  }
  .registration-aside .link-login:hover,
  .registration-aside .link-login:active{
    background-color: white;
    color: #4F802C;
    transition: ease-in 0.15s;
  }
  @media screen and (min-width: 768px){
    section{
      height: 500px;
    }
    h2{
      display: inline;
    }
    .registration-form{
      flex-basis: 50%;
      padding: 0px 40px;
    }
    .registration-aside{
      display: flex;
    }
    .privacy-terms{
      display: none;
    }
    .animate__animated{
      --animate-duration: 0s;
    }
  }