* {
  box-sizing: border-box;
  font-family: "Recursive", sans-serif;
}

.login-form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 80%;
  margin: 0 auto;
  padding: 10% 0;
  padding-bottom: 0;
}
.login-form input {
  box-sizing: border-box;
  font-weight: 500;
  font-size: 14px;
  border-radius: 25px;
  border: 1px solid rgb(175, 175, 175);
  height: 50px;
  margin-bottom: 15px;
  padding: 0px 24px;
}
.login-form input:focus {
  outline: none;
  border: 2px solid rgb(104, 103, 103);
}
.login-form button {
  box-sizing: border-box;
  font-weight: 500;
  font-size: 14px;
  border-radius: 25px;
  height: 50px;
  border: none;
  color: white;
  text-transform: uppercase;
  background-color: #50802C;
  cursor: pointer;
  margin: 15px 0px;
}
.login-form button:hover, 
.login-form button:active{
  background-color: #4c7929;
}
.login-form .login-message {
  color: lightgray;
  text-align: center;
  margin: 0;
  font-size: 14px;
}
.login-form .flex {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 70%;
  margin: 15px auto;
}

.flex-register {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  white-space: nowrap;
}
.flex-register p {
  font-weight: 500;
  font-size: 14px;
}
.flex-register .yellow {
  color: #FB8F07;
}
.flex-register .yellow a {
  color: #FB8F07;
  text-decoration: none;
  padding-left: 3px;
}

.privacy-terms {
  font-weight: 500;
  font-size: 12px;
  text-align: center;
}

.login-help {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 15px 0;
  font-size: 14px;
}

.login-checkbox {
  position: relative;
  color: #FB8F07;
  padding-left: 30px;
  cursor: pointer;
}

.login-checkbox:hover,
.login-checkbox:focus {
  color: #FB8F07;
}

.login-checkbox input[type=checkbox] + .checkbox-indicator {
  position: absolute;
  top: -2px;
  left: 0;
  width: 20px;
  height: 20px;
  border: 2px solid #000000;
}

.login-checkbox input[type=checkbox]:checked + .checkbox-indicator::before {
  content: "\f00c";
  font-family: "FontAwesome";
  position: relative;
  left: 1px;
  color: black;
}

.restore {
  color: #FB8F07;
  text-decoration: none;
  font-size: 14px;
}

.restore:hover,
.restore:focus {
  text-decoration: none;
}
.visually-hidden {
  display: none;
} 

.social a {
  text-align: center;
  width: 40px;
  height: 40px;
  float: left;
  margin: 0 10px 10px 0;
}

.social a img{
  width: 100%;
  height: 100%;
}

.animate__animated {
  --animate-duration: 0.5s;
}

h2{
  display: none;
  color: #FB8F07;
  font-size: 20px;
  text-align: center;
  padding: 30px 0px;
}
section{
  display: -webkit-flex;
  display: flex;
}
.login-aside{
  display: none;
  -webkit-flex-basis: 50%;
          flex-basis: 50%;
  background-color: #4F802C;  
  -webkit-flex-direction: column;  
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-clip-path: polygon(0 0, 100% 0, 90% 100%, 0% 100%);
          clip-path: polygon(0 0, 100% 0, 90% 100%, 0% 100%);
}
.login-aside h2{
 color: white;
 font-size: 25px;
}
.login-aside p{
  padding: 10px 0px;
}
.login-aside .link-register{
  width: 200px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  text-transform: uppercase;
  color: white;
  font-size: 12px;
  background-color: inherit;
  border: 1px solid white;
  border-radius: 5px;
  margin: 10px 0px;
}
.login-aside .link-register:hover, 
.login-aside .link-register:active{
  background-color: white;
  color: #4F802C;
  transition: ease-in 0.15s;
}
@media screen and (min-width: 768px){
  section{
    height: 500px;
  }
  h2{
    display: inline;
  }
  .login-aside{
    display: -webkit-flex;
    display: flex;
  }
  .login-form{
    -webkit-flex-basis: 50%;
            flex-basis: 50%;
    padding: 0px 40px;
  }
  .privacy-terms{
    display: none;
  }
  .animate__animated{
    --animate-duration: 0s;
  }
}
@media screen and (min-width: 1800px) {
	section {
		height: 90%;
	}
}
